import React, { useEffect, useRef, useState } from 'react';
import { Container, Box, Grid } from '@mui/material';
import Avatar from '@mui/material/Avatar';
import Slide from '@mui/material/Slide';

export function Home({ light }) {
  const [checked, setChecked] = useState(false);
  const containerRef = useRef(null);

  useEffect(() => {
    // Background();
    setChecked(true);
  }, [light]);

  return (
    <div className={'home_wrapper'} id={'about'}>
      <Box sx={{ mt: '10vh' }}>
        <Container maxwidth="fixed">
          <Box>
            <Grid container direction="column" alignItems="center" justifyContent="center">
              <Slide direction="left" in={checked} container={containerRef.current} timeout={800}>
                <Box>
                  <Box sx={{ fontSize: '7vh', textAlign: 'center' }}>
                    <b>Hey there!</b>
                  </Box>
                  <Box sx={{ fontSize: '5vh', textAlign: 'center', mb: '2vh' }}>
                    My name is Sergey Lazarkov
                  </Box>
                </Box>
              </Slide>
              <Slide direction="right" in={checked} container={containerRef.current} timeout={800}>
                <Grid>
                  <Avatar
                    alt="Avatar"
                    src="https://i.ibb.co/hVhFsDd/IMG-20230214-003934-003-01-01.jpg"
                    sx={{ width: '35vh', height: '35vh' }}
                  />
                </Grid>
              </Slide>
            </Grid>
            <Slide direction="up" in={checked} container={containerRef.current} timeout={800}>
              <Grid>
                <Box sx={{ mt: 5 }}>
                  <Box>
                    <p style={{ fontSize: '4vh' }} align="center">
                      <b>And I'm a Frontend Developer from Saint-Petersburg, Russia</b>
                    </p>
                  </Box>
                </Box>
              </Grid>
            </Slide>
          </Box>
        </Container>
      </Box>
    </div>
  );
}

